import { Component, Inject, OnInit } from "@angular/core";
import { UntypedFormControl, UntypedFormGroup } from "@angular/forms";
import { MatLegacyDialog as MatDialog } from "@angular/material/legacy-dialog";
import { IImageUploadService, PostImageUploadRequest, PostImageUploadResponse } from "../../../@intermediate/image-upload";
import { ImageCropDialogComponent, SnackBarService } from "@tei/material";
import { FieldConfig } from "../field.interface";
import { base64ToFile } from "ngx-image-cropper";


@Component({
  selector: "tei-tee-times-shared-image-upload",
  templateUrl: "./image-upload.component.html",
  styleUrls: ["./image-upload.component.css"],
  host: { 'class': 'o-setting' }

})
export class ImageUploadComponent implements OnInit
{
  selectedImage: any;
  ImageName: string;

  public field: FieldConfig;
  public group: UntypedFormGroup;

  constructor(public dialog: MatDialog,
    @Inject('IImageUploadService') private ImageUploadService: IImageUploadService,
    private _snackBar: SnackBarService,
  ) { }

  ngOnInit(): void
  {
    const jsonImageInfo = (this.group.get(this.field.name) as UntypedFormControl).value;

    if (jsonImageInfo)
    {
      const existingImage = JSON.parse(jsonImageInfo);

      if (existingImage)
      {
        this.ImageName = existingImage.ImageName;
        this.selectedImage = existingImage.ImagePath;
      }
    }

  }

  openDialog(): void
  {
    let data: any = {};

    const dialogRef = this.dialog.open(ImageCropDialogComponent, {
      width: '650px',
      data: { imgName: this.ImageName, croppedImage: this.selectedImage, origImage: this.selectedImage, CourseName: '' },
      panelClass: 'setting-image'
    });

    dialogRef.afterClosed().subscribe(result =>
    {
      if (result)
      {
        const formData = new FormData();
        const file = base64ToFile(result.croppedImage);
        formData.append("Image", file, result.imgName);
        this.ImageUploadService.Post(formData).subscribe(response => 
        {
          this.patchImageFormValue(result, response);
        }, error =>
        {
          if (error?.error?.errors?.Image?.length)
          {
            this._snackBar.showError(error?.error?.errors?.Image[0]);
          }

        });
      }
    });
  }

  private patchImageFormValue(result: any, response: PostImageUploadResponse)
  {
    this.group.patchValue({
      //Image: result.croppedImage,
      Image: response.FilePath,
      ImageName: result.imgName
    });

    this.ImageName = result.imgName;
    this.selectedImage = result.croppedImage;

    const savingImage = {
      Type: "Image",
      ImageName: result.imgName,
      ImagePath: response.FilePath,
      ImageType: "",
      //ImageBase64: result.croppedImage,
      FileUID: response.FileUID
    };

    (this.group.get(this.field.name) as UntypedFormControl).setValue(savingImage);
  }

  // openDialog(): void
  // {
  //   const data = new ImgDialogData();

  //   const dialogRef = this.dialog.open(ImageCropDialogComponent, {
  //     width: "450px",
  //     data: {
  //       imgName: data.imgName,
  //       croppedImage: data.croppedImage,
  //       origImage: data.origImage,
  //     },
  //   });

  //   dialogRef.afterClosed().subscribe((result) =>
  //   {
  //     if (result)
  //     {
  //       this.selectedImage = {
  //         ImageID: "1", //We set this to one so on the webapi we know it's a new Image
  //         ThumbnailPath: result.croppedImage,
  //         Caption: "",
  //       };

  //       (this.group.get(this.field.name) as FormControl).setValue(result.croppedImage);
  //     }
  //   });
  // }


  extractImageName()
  {
    return this.ImageName?.split("?")[0];
  }
}
