import { Component, OnInit } from '@angular/core';
import { UntypedFormGroup } from "@angular/forms";
import { FieldConfig } from '../field.interface';

@Component({
  selector: 'tei-common-module-settings-input',
  templateUrl: './input.component.html',
  styleUrls: ['./input.component.scss'],
  host: { 'class': 'o-setting' }
})
export class InputComponent implements OnInit
{
  public field: FieldConfig;
  public group: UntypedFormGroup;

  constructor()
  {

  }

  ngOnInit()
  {
  }

  onChangePin(event: string, controlName: string)
  {
    this.group.get(controlName).patchValue(event);
  }

  getFieldPropertyValue(input: string)
  {
    return this.field && this.field.properties && this.field.properties.length > 0 ? this.field.properties.find(x => x.name == input).value : "";
  }
}
