import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core'
import { IManageOverviewService, GetManageOverviewRequest, GetManageOverviewResponse, PostManageOverviewRequest, PostManageOverviewResponse } from '../@intermediate/manage-overview';

@Injectable({
  providedIn: 'root'
})

export class ManageOverviewService implements IManageOverviewService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/ModuleSettings/' + 'ManageOverview';
  }

  public Get(request: GetManageOverviewRequest): Observable<GetManageOverviewResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetManageOverviewResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostManageOverviewRequest): Observable<PostManageOverviewResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostManageOverviewResponse>(apiUrl, request);
  }
}
