import { Component, OnInit } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { FieldConfig } from '../field.interface';

@Component({
  selector: 'tei-common-module-settings-slidetoggle',
  templateUrl: './slidetoggle.component.html',
  styleUrls: ['./slidetoggle.component.scss'],
  host: { 'class': 'o-setting' }
})
export class SlidetoggleComponent implements OnInit
{
  field: FieldConfig;
  group: UntypedFormGroup;
  constructor()
  {

  }

  ngOnInit()
  {
  }
  getFieldPropertyValue(input: string)
  {
    return this.field && this.field.properties && this.field.properties.length > 0 && this.field.properties.find(x => x.name == input) ? this.field.properties.find(x => x.name == input).value : "";
  }
  get getLabel()
  {
    return this.group.get(this.field.name).value ? this.getFieldPropertyValue('trueText') : this.getFieldPropertyValue('falseText');
  }
}
