export class PostManageCategoryRequestForm extends UntypedFormGroup
{
  public Properties: PropertiesRequestArray;
  public Validations: ValidationsRequestArray;
  public Options: OptionsRequestArray;
  public Dependencies: DependenciesRequestArray;
  constructor(private ManageCategoryService: IManageCategoryService)
  {
    super({
      NavigationID: new UntypedFormControl(''),
      ID: new UntypedFormControl(''),
      Type: new UntypedFormControl(''),
      Name: new UntypedFormControl(''),
      UniqueName: new UntypedFormControl(''),
      Description: new UntypedFormControl(''),
      ParentID: new UntypedFormControl(''),
      SubRefID: new UntypedFormControl(''),
      DisplayType: new UntypedFormControl(''),
      MoreTypes: new UntypedFormControl(false),
      Properties: new PropertiesRequestArray(ManageCategoryService),
      Validations: new ValidationsRequestArray(ManageCategoryService),
      Options: new OptionsRequestArray(ManageCategoryService),
      Dependencies: new DependenciesRequestArray(ManageCategoryService),
      DependencyCondition: new UntypedFormControl('')
    });
    this.Properties = this.get('Properties') as PropertiesRequestArray;
    this.Validations = this.get('Validations') as ValidationsRequestArray;
    this.Options = this.get('Options') as OptionsRequestArray;
    this.Dependencies = this.get('Dependencies') as DependenciesRequestArray;
  }

  public value: PostManageCategoryRequest;

  public setValue(
    value: GetManageCategoryResponse,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    const request = new PostManageCategoryRequest();

    request.NavigationID = value.NavigationID;
    request.ID = value.ID;
    request.Type = value.Type;
    request.Name = value.Name;
    request.UniqueName = value.UniqueName;
    request.Description = value.Description;
    request.ParentID = value.ParentID;
    request.SubRefID = value.SubRefID;
    request.DisplayType = value.DisplayType;
    if (value.DisplayType)
    {
      request.Properties = value.Properties;
      this.Properties.setValue(value.Properties);
    }
    else
    {
      request.Properties = new Array<OptionHolder>();
    }
    request.Validations = value.Validations;
    this.Validations.setValue(value.Validations);
    if (value.DisplayType)
    {
      request.Options = value.Options;
      this.Options.setValue(value.Options);
    }
    else
    {
      request.Options = new Array<OptionHolder>();
    }
    if (value.DisplayType)
    {
      request.Dependencies = value.Dependencies;
      this.Dependencies.setValue(value.Dependencies);
    }
    else
    {
      request.Dependencies = new Array<OptionHolder>();
    }
    request.DependencyCondition = value.DependencyCondition;
    super.setValue(request, options);
  }
}

