import { Component, OnInit } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { ComponentLookup } from '../component-lookup';
import { FieldConfig } from '../field.interface';

@ComponentLookup('expansionpanel')
@Component({
  selector: 'tei-common-module-settings-expansionpanel',
  templateUrl: './expansionpanel.component.html',
  styleUrls: ['./expansionpanel.component.scss'],
  host: { 'class': 'o-setting' }
})
export class ExpansionPanelComponent implements OnInit
{
  field: FieldConfig;
  group: UntypedFormGroup;
  constructor()
  {

  }

  ngOnInit()
  {
    this.field.collection.forEach(x => { x.parentType = "expansionpanel"; });
  }
}
