import { NgModule } from '@angular/core';
import { ManageOverviewComponent } from './manage-overview.component';
import { ManageOverviewService } from '../../@services/manage-overview.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { MaterialModule } from '../dynamic-components/material.module';
import {MatRippleModule} from '@angular/material/core';


const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MaterialModule,MatRippleModule];
const TEI_MATERIAL_MODULES = [];

const COMPONENTS = [ManageOverviewComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IManageOverviewService', useClass: ManageOverviewService }, ],
})
export class ManageOverviewModule { }
