import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ActivatedRoute } from '@angular/router';
import { GetOverviewRequest, GetOverviewResponse, IOverviewService, PostOverviewRequestForm } from '../../@intermediate/overview';
import { ManageOrderComponent } from '../../components/manage-order/manage-order.component';
import { ManageOverviewComponent } from '../../components/manage-overview/manage-overview.component';
import { TranslateService } from '@ngx-translate/core';
import { BreadCrumb } from '@tei/material';

@Component({
  selector: 'tei-common-module-settings-pages-overview',
  templateUrl: './overview.component.html',
  styleUrls: ['./overview.component.scss']
})
export class OverviewComponent implements OnInit
{
  model: GetOverviewResponse;
  form: PostOverviewRequestForm;
  fileNameDialogRef: MatDialogRef<ManageOverviewComponent>;
  fileNameDialogRef1: MatDialogRef<ManageOrderComponent>;
  settingCategoryID: string;
  isLoading: boolean;

  breadCrumbData: BreadCrumb[] = [
    {
      title: this.translate.instant('MODULE_SETTINGS.OVERVIEW.SETUP'),
      relativeUrl: '/office/bookings/setup'
    },
    {
      title: this.translate.instant("MODULE_SETTINGS.OVERVIEW.CONTACT_LOOKUP_TEMPLATE"),
      relativeUrl: './'
    }
  ];

  constructor(@Inject('IOverviewService') private OverviewService: IOverviewService,
    private dialog: MatDialog,
    private route: ActivatedRoute,
    private translate: TranslateService)
  {
    this.route.params.subscribe(params =>
    {
      if (this.settingCategoryID !== params.id)
      {
        this.settingCategoryID = params.id;

        const request = new GetOverviewRequest();
        request.SettingCategoryID = this.settingCategoryID;
        this.load(request);
      }
    });
  }

  ngOnInit()
  {
    this.form = new PostOverviewRequestForm();
    // const request = new GetOverviewRequest();
    // this.load(request);
  }

  private load(request: GetOverviewRequest)
  {
    if (!this.model)
    {
      this.model = new GetOverviewResponse();
    }
    this.turnOnProgressBar();

    this.OverviewService.Get(request).subscribe(response =>
    {
      this.turnOffProgressBar();
      this.model = response;

      this.setDevelopmentEnvSettings();

    }, error =>
    {
      this.turnOffProgressBar();
    });
  }

  turnOffProgressBar()
  {
    this.isLoading = false;
  }

  turnOnProgressBar()
  {
    this.isLoading = true;
  }

  setDevelopmentEnvSettings()
  {
    if (window.location.host.includes("14200"))
    {
      this.model.IsEntityEditable = true;
    } else
    {
      this.model.IsEntityEditable = false;
    }
  }

  addOverviewElement()
  {
    this.fileNameDialogRef = this.dialog.open(ManageOverviewComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      width: '600px',
      data: { message: this.settingCategoryID }
    });
    this.fileNameDialogRef.afterClosed().subscribe(result =>
    {
      this.model.fieldConfigs = result && result.data ? result.data : this.model.fieldConfigs;
    });
  }
  editOverviewElement()
  {
    this.fileNameDialogRef1 = this.dialog.open(ManageOrderComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      width: '600px',
      data: { message: this.settingCategoryID, title: 'OVERVIEW' }
    });
    this.fileNameDialogRef1.afterClosed().subscribe(result =>
    {
      this.model.fieldConfigs = result && result.data ? result.data : this.model.fieldConfigs;
    });
  }
}
