import { Component, OnInit } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { FieldConfig } from '../field.interface';

@Component({
  selector: 'tei-common-module-settings-booking-requirements-list',
  templateUrl: './booking-requirements-list.component.html',
  styleUrls: ['./booking-requirements-list.component.scss']
})
export class BookingRequirementsListComponent implements OnInit {
  field: FieldConfig;
  group: UntypedFormGroup;
  parentSettingCategoryID: string;
  refID: string;

  constructor(
    private router: Router,
    private route: ActivatedRoute) {
    this.route.params.subscribe(params => {
      this.parentSettingCategoryID = params.id;//ParentCategoryID
      this.refID = params.refID;
    });
  }

  ngOnInit() {

  }

  getModuleSettings($event) {
    let settingID = this.field.name;
    let templateID = $event.TemplateUID;
    this.router.navigate(['/office/portal-admin-center/setup/web-admin-center/detail/' + settingID + "/" + templateID]);
  }


}
