import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyDialogModule as MatDialogModule, MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { MatDividerModule } from '@angular/material/divider';
import { MatLegacyFormFieldModule as MatFormFieldModule } from '@angular/material/legacy-form-field';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { ImageUploadService } from '../../../@services/image-uplaod.service';
import { ImageCropDialogModule, SnackBarModule } from '@tei/material';
import { ImageCropperModule } from 'ngx-image-cropper';
import { ImageCropDialogComponent } from '../imagecropper/image-crop-dialog.component';
import { CustomCropDialogComponent } from './custom-crop-dialog/custom-crop-dialog.component';
import { ImageUploadComponent } from './image-upload.component';


const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatSelectModule, MatFormFieldModule, MatDividerModule, MatButtonModule, MatDialogModule, MatIconModule];
const TEI_MATERIAL_MODULES = [ImageCropperModule, ImageCropDialogModule, SnackBarModule];

const COMPONENTS = [ImageCropDialogComponent, ImageUploadComponent, CustomCropDialogComponent];

@NgModule({
    declarations: [...COMPONENTS],
    imports: [
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...TEI_MATERIAL_MODULES
    ],
    exports: [
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...TEI_MATERIAL_MODULES,
        ...COMPONENTS
    ],
    providers: [
        {
            provide: MatDialogRef,
            useValue: {}
        },
        {
            provide: 'IImageUploadService', useClass: ImageUploadService
        },
        {
            provide: MAT_DIALOG_DATA, useValue: {}
        }
    ]
})
export class ImageUploadModule { }
