import { CdkDragDrop, moveItemInArray } from '@angular/cdk/drag-drop';
import { Component, Inject, OnInit, SkipSelf } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { DialogData } from '@tei/material';
import { GetManageOrderRequest, GetManageOrderResponse, IManageOrderService, PostManageOrderRequestForm } from '../../@intermediate/manage-order';
import { MatLegacySelectionListChange as MatSelectionListChange } from '@angular/material/legacy-list';

@Component({
  selector: 'tei-common-module-settings-manage-order',
  templateUrl: './manage-order.component.html',
  styleUrls: ['./manage-order.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(ManageOrderComponent)
  ]
})
export class ManageOrderComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetManageOrderResponse;
  form: PostManageOrderRequestForm;
  navigationID: string;
  navigation: Array<string> = [];

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('IManageOrderService') private ManageOrderService: IManageOrderService,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    public dialogRef: MatDialogRef<ManageOrderComponent>)
  {
    super(_errorStateMatcher);
  }

  ngOnInit()
  {
    this.form = new PostManageOrderRequestForm();
    const request = new GetManageOrderRequest();
    this.navigationID = this.data.message;
    request.ID = this.data.message;
    request.Type = this.data.title;
    this.load(request);
  }

  private load(request: GetManageOrderRequest)
  {
    if (!this.model)
    {
      this.model = new GetManageOrderResponse();
    }

    this.ManageOrderService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.writeValue(this.model);
    });
  }

  drop(event: CdkDragDrop<string[]>)
  {
    moveItemInArray(this.model.ListFieldItems, event.previousIndex, event.currentIndex);

    var index = 0;
    this.model.ListFieldItems.forEach(x =>
    {
      x.Order = index++;
    });
  }

  save(form)
  {
    form.value.ID = this.navigationID;
    form.value.Type = this.data.title;
    form.controls.ListFieldItems = this.model.ListFieldItems;
    this.ManageOrderService.Post(form.value).subscribe(response =>
    {
      this.dialogRef.close({ data: response.fieldConfigs });
    });
  }

  navigate(ID, needToPush)
  {
    if (needToPush)
      this.navigation.push(this.form.value.ID);
    const request = new GetManageOrderRequest();
    request.ID = ID;
    request.Type = this.data.title;
    this.load(request);
  }

  selectionChange(event: MatSelectionListChange)
  {
    this.model.ListFieldItems.find(x => x.ID == event.options[0].value).IsDeleted = !this.model.ListFieldItems.find(x => x.ID == event.options[0].value).IsDeleted;
  }

  back()
  {
    var navigateID = this.navigation.pop();
    this.navigate(navigateID, false);
  }

}
