import { NgModule } from '@angular/core';
import { RouterModule, Routes } from '@angular/router';
import { RouteData } from '@tei/core';
import { ModuleSettingsComponent } from './module-settings.component';
import { DefaultComponent } from './pages/default/default.component';
import { DetailsComponent } from './pages/details/details.component';
import { OverviewComponent } from './pages/overview/overview.component';
import { PagesComponent } from './pages/pages.component';

const routes: Routes = [{
  path: '',
  component: ModuleSettingsComponent,
  children: [
    {
      path: '',
      component: PagesComponent,
      children: [
        {
          path: 'default',
          component: DefaultComponent,
          data: { RouteData: new RouteData({ Title: 'Module Settings' }) }
        },
        {
          path: 'detail/:id',
          component: DetailsComponent,
          data: { RouteData: new RouteData({ Title: 'Module Settings', ShowBottomToolbar: false }) }
        },
        {
          path: 'detail/:id/:refID/:settingID/:refName',
          component: DetailsComponent,
          data: { RouteData: new RouteData({ Title: 'Module Settings', ShowBottomToolbar: false }) }
        },
        {
          path: 'detail/:id/:refID',
          component: DetailsComponent,
          data: { RouteData: new RouteData({ Title: 'Modsule Settings', ShowBottomToolbar: false }) }
        },
        {
          path: '',
          redirectTo: 'overview/',
          pathMatch: 'full'
        },
        {
          path: 'overview/:id',
          component: OverviewComponent,
          data: { RouteData: new RouteData({ Title: 'Module Settings' }) }
        }
      ]
    },
    { path: '**', redirectTo: '' },
  ],
}];


@NgModule({
  imports: [RouterModule.forChild(routes)],
  exports: [RouterModule],
})
export class ModuleSettingsRoutingModule
{
}
