import { HttpClient, HttpHeaders } from '@angular/common/http';
import { Inject, Injectable } from '@angular/core';
import { IAPIBaseUrlProvider } from '@tei/core'
import { Observable } from 'rxjs';
import { GetDetailsRequest, GetDetailsResponse, IDetailsService, PostDetailsRequest, PostDetailsResponse } from '../@intermediate/details';
import { PostEffectsRequest, PostEffectsResponse } from '../@intermediate/details/models/post-effects.model';

@Injectable({
  providedIn: 'root'
})

export class DetailsService implements IDetailsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/ModuleSettings/' + 'Details';
  }

  public Get(request: GetDetailsRequest): Observable<GetDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '/GetDetails';
    //return this.http.get<GetDetailsResponse>(apiUrl, { params: request as any });
    return this.http.post<GetDetailsResponse>(apiUrl, request);
  }

  public PostEffects(request: PostEffectsRequest): Observable<PostEffectsResponse>
  {
    const apiUrl = this.serviceUrl + '/PostEffects';
    const httpOptions = {
      headers: new HttpHeaders({
        'ignoreLoadingBar': ''
      })
    };
    return this.http.post<PostEffectsResponse>(apiUrl, request);
  }

  public Post(request: PostDetailsRequest): Observable<PostDetailsResponse>
  {
    const apiUrl = this.serviceUrl + '/PostDetails';
    return this.http.post<PostDetailsResponse>(apiUrl, request);
  }
}
