import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialog as MatDialog, MatLegacyDialogRef as MatDialogRef } from '@angular/material/legacy-dialog';
import { ActivatedRoute } from '@angular/router';
import { SnackBarService } from '@tei/material';
import { GetDetailsRequest, GetDetailsResponse, IDetailsService, PostDetailsRequestForm } from '../../@intermediate/details';
import { PostEffectsRequest } from '../../@intermediate/details/models/post-effects.model';
import { DynamicFormService } from '../../components/dynamic-components/dynamic-form/dynamic-form.service';
import { ManageCategoryComponent } from '../../components/manage-category/manage-category.component';
import { ManageOrderComponent } from '../../components/manage-order/manage-order.component';

@Component({
  selector: 'tei-common-module-settings-pages-details',
  templateUrl: './details.component.html',
  styleUrls: ['./details.component.scss']
})
export class DetailsComponent implements OnInit
{
  model: GetDetailsResponse;
  form: PostDetailsRequestForm;
  fileNameDialogRef: MatDialogRef<ManageCategoryComponent>;
  fileNameDialogRef1: MatDialogRef<ManageOrderComponent>;
  settingCategoryID: string;
  needsRefresh: number;
  settingID: string;
  settingCategoryName: string;
  settingCategoryNameIdentifier: string;
  isLoading: boolean;

  constructor(@Inject('IDetailsService') private DetailsService: IDetailsService,
    private dynamicfilter: DynamicFormService,
    private dialog: MatDialog,
    private snackbarService: SnackBarService,
    private route: ActivatedRoute)
  {
    this.route.params.subscribe(params =>
    {
      this.settingCategoryID = params.id;
      this.settingID = params.settingID;
      this.settingCategoryName = params.refName;
      this.settingCategoryNameIdentifier = params.refID;
    });
  }

  ngOnInit()
  {
    this.needsRefresh = 0;
    this.form = new PostDetailsRequestForm();
    const request = new GetDetailsRequest();
    this.load(request);

    this.dynamicfilter.currentFilter.subscribe(filterValue =>
    {
      if (filterValue)
      {
        if (filterValue.Type && filterValue.Type == "control")
        {
          if (filterValue.Field.hasEffect)
          {
            this.postEffects(filterValue);
            this.form.controls.FilterValues.setValue(filterValue.Group);
          }
        }
        else if (filterValue.Type == "NewGroup")
        {
          this.form.controls.FilterValues.setValue(filterValue.Group);
        }
        else
        {
          var bCallService = false;
          if (this.form.value.FilterValues !== null)
          {
            for (var element in filterValue)
            {
              if (element.startsWith("D-") && this.form.value.FilterValues[element] != filterValue[element])
              {
                bCallService = true;
                break;
              }
            }
          }
          else
          {
            bCallService = true;
          }

          if (bCallService)
          {
            const request = new GetDetailsRequest();
            request.FilterValues = filterValue;
            this.load(request, true);
          }

          this.updateFormGroupValidation(filterValue);
        }
      }
    });
  }

  private updateFormGroupValidation(filterValue: any)
  {
    if (filterValue?.Status == "VALID")
    {
      this.form.setErrors(null);
      this.form.controls.FilterValues.setValue(filterValue?.FormValue);
    }
    else
    {
      this.form.setErrors({ "INVALID": true });
    }
  }

  private load(request: GetDetailsRequest, val: boolean = false)
  {
    if (!this.model)
    {
      this.model = new GetDetailsResponse();
    }
    this.turnOnProgressBar();
    request.SettingCategoryID = this.settingCategoryID;
    request.SettingID = this.settingID;
    request.SettingCategoryName = this.settingCategoryName;
    request.SettingCategoryNameIdentifier = this.settingCategoryNameIdentifier;

    this.DetailsService.Get(request).subscribe(response =>
    {
      this.turnOffProgressBar();
      this.model = response;
      if (val)
        this.needsRefresh++;

      this.setDevelopmentEnvSettings();

    }, error =>
    {
      this.turnOffProgressBar();
    });
  }

  turnOffProgressBar()
  {
    this.isLoading = false;
  }

  turnOnProgressBar()
  {
    this.isLoading = true;
  }


  setDevelopmentEnvSettings()
  {
    if (window.location.host.includes("14200"))
    {
      this.model.IsEntityEditable = true;
    } else
    {
      this.model.IsEntityEditable = false;
    }
  }

  private postEffects(filterValue: any)
  {
    this.turnOnProgressBar();
    const request = new PostEffectsRequest();
    request.FilterValues = filterValue.Group;
    request.fieldConfigs = this.model.fieldConfigs;
    request.Value = filterValue.Value;

    let settingID: string = filterValue.Field.name;

    if (filterValue.Field.name)
    {
      let FieldName = (filterValue.Field.name as string);
      settingID = FieldName.replace("D-", "");

      const settingIDSplitted = settingID.split("_");
      settingID = settingIDSplitted[0];

      if (settingIDSplitted.length >= 2)
      {
        let index = Number.parseInt(settingIDSplitted[1]);
        request.Index = index;
      }
    }
    request.SettingCategoryID = this.settingCategoryID;
    request.SettingID = settingID;
    request.SettingCategoryName = this.settingCategoryName;
    request.SettingCategoryNameIdentifier = this.settingCategoryNameIdentifier;

    const scrollYPos = window.scrollY;

    this.DetailsService.PostEffects(request).subscribe(response =>
    {
      this.turnOffProgressBar();
      if (response)
      {
        this.model.fieldConfigs = response.fieldConfigs;

        this.needsRefresh++;

        //This is to keep state of scroll!
        setTimeout(() =>
        {
          window.scroll(0, scrollYPos);
        }, 1);
      }
    }, error =>
    {
      this.turnOffProgressBar();
    });
  }

  save(form: PostDetailsRequestForm)
  {
    form.patchValue({
      SettingCategoryNameIdentifier: this.settingCategoryNameIdentifier
    });
    this.turnOnProgressBar();
    this.DetailsService.Post(form.value).subscribe(response =>
    {
      this.turnOffProgressBar();
      this.snackbarService.showSuccess('The settings have been saved successfully');
    }, error =>
    {
      this.turnOffProgressBar();
    });
  }

  openCategory()
  {
    this.fileNameDialogRef = this.dialog.open(ManageCategoryComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      width: '600px',
      data: { settingCategoryID: this.settingCategoryID, refID: this.settingCategoryNameIdentifier }
    });
    this.fileNameDialogRef.afterClosed().subscribe(result =>
    {
      this.model.fieldConfigs = result && result.data ? result.data : this.model.fieldConfigs;
    });
  }

  editOverviewElement()
  {
    this.fileNameDialogRef1 = this.dialog.open(ManageOrderComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      width: '600px',
      data: { message: this.settingCategoryID, title: 'DETAIL' }
    });
    this.fileNameDialogRef1.afterClosed().subscribe(result =>
    {
      this.model.fieldConfigs = result && result.data ? result.data : this.model.fieldConfigs;
    });
  }

  goBack()
  {
    window.history.back();
  }

}
