import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { GetDetailsResponse } from './get-details.model';

export class PostDetailsRequest
{
  public FilterValues: any[];
  public SettingCategoryNameIdentifier: string;
}

export class PostDetailsRequestForm extends UntypedFormGroup
{
  //FilterValues: any;
  constructor()
  {
    super({
      FilterValues: new UntypedFormControl(''),
      SettingCategoryNameIdentifier: new UntypedFormControl(''),
    });
  }

  public value: PostDetailsRequest;

  public setValue(value: GetDetailsResponse)
  {
    const request = new PostDetailsRequest();
    request.FilterValues = value.FilterValues;
    request.SettingCategoryNameIdentifier = '';

    super.setValue(request);
  }
}

export class PostDetailsResponse
{

}
