import { UntypedFormArray, UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { DependencyItemForm, OptionHolder } from '../../manage-category';
import { IManageOverviewService } from '../manage-overview.interface';
import { FieldConfig } from './../../../../lib/components/dynamic-components/field.interface';
import { GetManageOverviewResponse } from './get-manage-overview.model';

export class PostManageOverviewRequest
{
  public ID: string;
  public Name: string;
  public Icon: string;
  public Description: string;
  public VersionIdentifier: string;
  public Navigation: string;
  public ParentID: string;
  public Dependencies: OptionHolder[];
  public DependencyConditions: OptionHolder[];
  public DependencyCondition: string;
  public PageLink: string;
}

class DependenciesRequestArray extends UntypedFormArray
{
  constructor(private ManageOverviewService: IManageOverviewService)
  {
    super([

    ]);
  }

  public setValue(dependencies: OptionHolder[])
  {
    this.clear();

    dependencies.forEach(item =>
    {
      const validationControl = new DependencyItemForm(item);
      this.push(validationControl);
    });
  }
}

export class PostManageOverviewRequestForm extends UntypedFormGroup
{
  public Dependencies: DependenciesRequestArray;
  constructor(private ManageOverviewService: IManageOverviewService)
  {
    super({
      ID: new UntypedFormControl(''),
      Name: new UntypedFormControl(''),
      Description: new UntypedFormControl(''),
      Navigation: new UntypedFormControl(''),
      Icon: new UntypedFormControl(''),
      ParentID: new UntypedFormControl(''),
      VersionIdentifier: new UntypedFormControl(''),
      Dependencies: new DependenciesRequestArray(ManageOverviewService),
      DependencyCondition: new UntypedFormControl(''),
      PageLink: new UntypedFormControl('')
    });
    this.Dependencies = this.get('Dependencies') as DependenciesRequestArray;
  }

  public value: PostManageOverviewRequest;

  public setValue(
    value: GetManageOverviewResponse,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    const request = new PostManageOverviewRequest();
    request.ID = value.ID;
    request.Name = value.Name;
    request.Description = value.Description;
    request.Navigation = value.Navigation;
    request.Icon = value.Icon;
    request.ParentID = (value.ParentID == '' ? null : value.ParentID);
    request.VersionIdentifier = value.VersionIdentifier;
    request.DependencyCondition = value.DependencyCondition;
    request.PageLink = value.PageLink;
    if (value.Dependencies)
      request.Dependencies = value.Dependencies;
    else
      request.Dependencies = new Array<OptionHolder>();
    request.DependencyCondition = value.DependencyCondition;
    super.setValue(request, options);
  }
}

export class PostManageOverviewResponse
{
  public fieldConfigs: FieldConfig[];
}
