import { Component, EventEmitter, Inject, Input, OnInit, Output } from '@angular/core';
import { UntypedFormBuilder, UntypedFormGroup, Validators } from '@angular/forms';
import { MatLegacyDialog as MatDialog, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { FieldConfig, FieldValidator } from '../field.interface';
import { DynamicFormService } from './dynamic-form.service';

@Component({
  exportAs: "tei-common-module-settings-dynamic-form",
  selector: 'tei-common-module-settings-dynamic-form',
  templateUrl: './dynamic-form.component.html',
  styleUrls: ['./dynamic-form.component.scss']
})
export class DynamicFormComponent implements OnInit
{
  @Input() fields: FieldConfig[] = [];
  @Output() submit: EventEmitter<any> = new EventEmitter<any>();
  @Input() group: UntypedFormGroup;
  _reload: number = 1;

  //This will recreate the form group from scratch. I don't know why!
  @Input() public set reload(val: number)
  {
    if (this._reload < val)
    {
      this.ngOnInit();
    }
    this._reload = val;
  }
  //form: FormGroup;
  formValues: any[] = [];
  constructor(
    @Inject(MAT_DIALOG_DATA) public data: any,
    private dialogRef: MatDialog,
    private fb: UntypedFormBuilder,
    private dynamicfilter: DynamicFormService
  )
  { }

  ngOnInit()
  {
    this.fields = (this.data && this.data.filterdata) ? this.data.filterdata : this.fields;
    this.group = this.createControl();

    this.dynamicfilter.emitValue({
      Type: "NewGroup",
      Group: this.group.value
    });
    this.group.valueChanges.subscribe((value) =>
    {
      this.formValues.push(value);
      setTimeout(() =>
      {
        if (this.formValues.length > 1)
        {
          this.emitDynamicFilterFormValues(1);
        }
        if (this.formValues.length == 1)
        {
          this.emitDynamicFilterFormValues(0);
        }
      }, 100);
    });
  }

  private emitDynamicFilterFormValues(index: number)
  {
    this.dynamicfilter.emitValue(
      {
        "FormValue": this.formValues[index],
        "Status": this.group.status
      });
    this.formValues = [];
  }

  onSubmit(event: Event)
  {
    event.preventDefault();
    event.stopPropagation();
    if (this.group.valid)
    {
      this.submit.emit(this.group.value);
    } else
    {
      this.validateAllFormFields(this.group);
    }
    this.dialogRef.closeAll();
  }

  createControl()
  {
    var group = this.fb.group({});

    this.fields.forEach(field =>
    {
      if (field.type === "permissionstable")
      {
        field.permissionsTableColletion.forEach(f =>
        {
          f.forEach(element =>
          {
            this.addToGroup(group, element);
          });

        });

      } else
      {
        this.addToGroup(group, field);
      }

    });

    return group;
  }

  addToGroup(group: UntypedFormGroup, field: FieldConfig)
  {
    if (field.type === "button") return;
    if (field.collection)
    {
      field.collection.forEach(f =>
      {
        this.addToGroup(group, f);
      });
    }
    else
    {
      const control = this.fb.control(
        field.value,
        this.bindValidations(field.validations || [])
      );

      control.valueChanges.subscribe(value =>
      {
        setTimeout(() =>
        {
          field.value = value;

          this.dynamicfilter.emitValue({
            Type: "control",
            Field: field,
            Value: value,
            Group: group.value,
            Status: group.status
          });
        }, 100);
      });

      group.addControl(field.name, control);
    }
  }

  bindValidations(validations: FieldValidator[])
  {
    if (validations.length > 0)
    {
      const validList = [];
      validations.forEach(valid =>
      {
        validList.push(this.validatorFactory(valid).validator);
      });
      return Validators.compose(validList);
    }
    return null;
  }

  validateAllFormFields(formGroup: UntypedFormGroup)
  {
    Object.keys(formGroup.controls).forEach(field =>
    {
      const control = formGroup.get(field);
      control.markAsTouched({ onlySelf: true });
    });
  }

  validatorFactory(val: FieldValidator)
  {
    switch (val.validator)
    {
      case 'min':
        return { name: val.name, message: val.message, validator: Validators.min(Number(val.condition)) };
      case 'max':
        return { name: val.name, message: val.message, validator: Validators.max(Number(val.condition)) };
      case 'required':
        return { name: val.name, message: val.message, validator: Validators.required };
      case 'requiredTrue':
        return { name: val.name, message: val.message, validator: Validators.requiredTrue };
      case 'minLength':
        return { name: val.name, message: val.message, validator: Validators.minLength(Number(val.condition)) };
      case 'maxLength':
        return { name: val.name, message: val.message, validator: Validators.maxLength(Number(val.condition)) };
      case 'pattern':
        return { name: val.name, message: val.message, validator: Validators.pattern(val.condition) };
      case 'nullValidator':
        return { name: val.name, message: val.message, validator: Validators.nullValidator };
      default: return null;
    }
  }
}
