import { Component, OnInit } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { FieldConfig } from '../field.interface';

@Component({
  selector: 'tei-common-module-settings-select',
  templateUrl: './select.component.html',
  styleUrls: ['./select.component.scss'],
  host: { 'class': 'o-setting' }
})
export class SelectComponent implements OnInit
{
  field: FieldConfig;
  group: UntypedFormGroup;
  constructor()
  {

  }

  ngOnInit()
  {
    if (this.field.parentType == "expansionpanel")
    {
      this.field.formControlView = 1;
    }

  }
  // getFieldPropertyValue(input: string)
  // {
  //   return this.field && this.field.properties && this.field.properties.length > 0 && this.field.properties.find(x => x.name == input) ? this.field.properties.find(x => x.name == input).value : "";
  // }
  get getLabel()
  {
    const groupFiled = this.group.get(this.field.name);

    if (groupFiled && groupFiled.value)
    {
      const fieldOption = this.field.options.find(x => x.value == groupFiled.value);

      if (fieldOption)
      {
        return fieldOption.icon;
      }
    }

    return "";
  }
}
