import { Component, OnInit } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { FieldConfig } from '../field.interface';

@Component({
  selector: 'tei-common-module-settings-cardlink',
  templateUrl: './cardlink.component.html',
  styleUrls: ['./cardlink.component.scss']
})
export class CardLinkComponent implements OnInit
{
  field: FieldConfig;
  group: UntypedFormGroup;
  constructor(private router: Router)
  {

  }

  ngOnInit()
  {

  }
  onCardClick()
  {
    ///////TODO this logic should be removed from here and move to BE
    if (!this.getFieldPropertyValue('disabled'))
    {
      if (this.field && this.getFieldPropertyValue('url') === 'detail')
      {
        this.router.navigate(['/office/portal-admin-center/setup/web-admin-center/detail/' + this.field.name]);
      }
      else if (this.field && this.getFieldPropertyValue('url') === 'overview')
      {
        this.router.navigate(['/office/portal-admin-center/setup/web-admin-center/overview/' + this.field.name]);
      }
      else
      {
        const token = this.getFieldPropertyValue('url').replace('[', '').replace(']', '');
        if (this.field.settings.filter(x => x.AccessibilityKey.includes(token)).length != 0)
        {
          let result = this.field.settings.filter(x => x.AccessibilityKey.includes(token)).map((v) => this.field.settingValues.filter(b => b.SettingID === v.SettingID));
          let path = result[0].find(x => x.SettingValue).SettingValue;
          if (this.field.nameIdentifier == "Categories")
          {
            path = path + "categories";
          }
          if (this.field.nameIdentifier == "Item Manager")
          {
            path = path + "item-manager";
          }
          if (this.field.nameIdentifier == "Dynamic Pricing")
          {
            path = path + "dynamic-pricing";
          }

          window.location.href = path;
        }
        else
        {
          window.location.href = this.getFieldPropertyValue('url');
        }
      }
    }
  }
  getFieldPropertyValue(input: string)
  {
    return this.field && this.field.properties && this.field.properties.length > 0 && this.field.properties.find(x => x.name == input) ? this.field.properties.find(x => x.name == input).value : "";
  }
}
