import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core'
import { IOverviewService, GetOverviewRequest, GetOverviewResponse, PostOverviewRequest, PostOverviewResponse } from '../@intermediate/overview';

@Injectable({
  providedIn: 'root'
})

export class OverviewService implements IOverviewService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/ModuleSettings/' + 'Overview';
  }

  public Get(request: GetOverviewRequest): Observable<GetOverviewResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetOverviewResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostOverviewRequest): Observable<PostOverviewResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostOverviewResponse>(apiUrl, request);
  }
}
