import { Component, Inject, OnInit } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { ImgDialogData } from '@tei/material';
import { ImageCropDialogComponent } from '../../imagecropper/image-crop-dialog.component';

@Component({
  selector: 'tei-tee-times-custom-crop-dialog',
  templateUrl: './custom-crop-dialog.component.html',
  styleUrls: ['./custom-crop-dialog.component.css']
})
export class CustomCropDialogComponent extends ImageCropDialogComponent implements OnInit
{

  localData: any;
  constructor(dialogRef: MatDialogRef<ImageCropDialogComponent>, @Inject(MAT_DIALOG_DATA) public data: ImgDialogData)
  {
    super(dialogRef, data);

    if (data)
    {
      this.localData = data;
    }
  }



}
