import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core';
import { IImageUploadService, PostImageUploadRequest, PostImageUploadResponse } from '../@intermediate/image-upload';

@Injectable({
  providedIn: 'root'
})

export class ImageUploadService implements IImageUploadService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/ModuleSettings/' + 'ImageUpload';
  }

  public Post(request: FormData): Observable<PostImageUploadResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostImageUploadResponse>(apiUrl, request);
  }
}
