import { UntypedFormControl, UntypedFormGroup, NumberValueAccessor, Validators } from "@angular/forms";
import { GetBookingRequirementsTemplateDataResponse, TemplateType } from "../../booking-requirements";


export class PostBookingRequirementsTemplateDataRequest
{
  public TemplateID: Number;
  public TemplateUID: string;
  public TemplateName: string;
  public Description: string;
  public TemplateType: TemplateType;
  public ModuleID: number;
}

export class PostBookingRequirementsTemplateDataResponse
{
}

export class PostBookingRequirementsTemplateForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      TemplateID: new UntypedFormControl(),
      TemplateUID: new UntypedFormControl(''),
      TemplateName: new UntypedFormControl('', Validators.required),
      Description: new UntypedFormControl(''),
      TemplateType: new UntypedFormControl({ value: '', disabled: true }),
    });
  }

  public value: PostBookingRequirementsTemplateDataRequest;

  public setValue(value: GetBookingRequirementsTemplateDataResponse)
  {
    const request = new PostBookingRequirementsTemplateDataRequest();
    request.TemplateUID = value.Templates[0]?.TemplateUID;
    request.TemplateID = value.Templates[0]?.TemplateID;
    request.TemplateName = value.Templates[0]?.Name;
    request.Description = value.Templates[0]?.Description;
    request.TemplateType = value.TemplateTypes.find(f => f.Selected == true);

    super.setValue(request);
  }
}
