import { Component, OnInit } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { ActivatedRoute, Router } from '@angular/router';
import { FieldConfig } from '../field.interface';

@Component({
  selector: 'tei-common-module-settings-dining',
  templateUrl: './restaurantsetting.component.html',
  styleUrls: ['./restaurantsetting.component.scss']
})
export class RestaurantSettingComponent implements OnInit {
  field: FieldConfig;
  group: UntypedFormGroup;
  settingCategoryID: string;
  refID: string;

  constructor(
    private router: Router,
    private route: ActivatedRoute) {
    this.route.params.subscribe(params => {
      this.settingCategoryID = params.id;//ParentCategoryID
      this.refID = params.refID;
    });
  }

  ngOnInit() {

  }

  diningDetails($event) {
    // console.log($event);
    const settingName = this.field.label;
    const settingID = this.field.name;
    //this.router.navigate(['detail/' + this.settingCategoryID + "/" + $event["course.CourseID"] + "/" + settingID + "/" + $event["course.CourseName"]]);
    const settings = $event["settings"];
    const restaurant = $event["restaurant"];
    let restaurantID = restaurant.RestaurantID as string;
    //window.location.assign(window.location.origin + baseUrl + "settings" + "/" + course.CourseID + "/" + CourseName);
    //this.router.navigate(['detail/' + this.settingCategoryID + "/" + course.CourseID + "/" + settingID + "/" + course.CourseName]);
    this.router.navigate(['/office/portal-admin-center/setup/web-admin-center/detail/' + settingID + "/" + restaurantID]);
  }
}
