import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { RouterModule } from '@angular/router';
import { QuillModule } from 'ngx-quill';
import { CardComponent } from './components/dynamic-components/card/card.component';
import { ExpansionPanelComponent } from './components/dynamic-components/expansionpanel/expansionpanel.component';
import { MaterialModule } from './components/dynamic-components/material.module';
import { PermissionsTableComponent } from './components/dynamic-components/permissionstable/permissionstable.component';
import { ManageCategoryModule } from './components/manage-category/manage-category.module';
import { ManageOrderModule } from './components/manage-order/manage-order.module';
import { ManageOverviewModule } from './components/manage-overview/manage-overview.module';
import { ModuleSettingsRoutingModule } from './module-settings-routing.module';
import { ModuleSettingsComponent } from './module-settings.component';
import { DefaultModule } from './pages/default/default.module';
import { DetailsModule } from './pages/details/details.module';
import { OverviewModule } from './pages/overview/overview.module';
import { PagesModule } from './pages/pages.module';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule];
const MODULES = [DefaultModule, PagesModule, DetailsModule, ManageCategoryModule,
  ManageOrderModule, ManageOverviewModule, OverviewModule, MaterialModule,
  QuillModule.forRoot()];

const COMPONENTS = [ModuleSettingsComponent];
const PIPES = [];

@NgModule({
  imports: [ModuleSettingsRoutingModule, ...BASE_MODULES, ...MODULES],
  exports: [...BASE_MODULES, ...MODULES, ...COMPONENTS, ...PIPES],
  declarations: [...COMPONENTS, ...PIPES],
  providers: [],

})
export class ModuleSettingsModule
{
  //This list is to prevent tree shaking to remove these components since they are adding dynamically
  static entryComponents = [CardComponent, PermissionsTableComponent, ExpansionPanelComponent];
}
