import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
import { RouterModule } from '@angular/router';
import { LoadingModule, SnackBarModule } from '@tei/material';
import { DetailsService } from '../../@services/details.service';
import { DynamicFormModule } from '../../components/dynamic-components/dynamic-form/dynamic-form.module';
import { MaterialModule } from '../../components/dynamic-components/material.module';
import { ManageCategoryComponent } from '../../components/manage-category/manage-category.component';
import { ManageCategoryModule } from '../../components/manage-category/manage-category.module';
import { ManageOrderComponent } from '../../components/manage-order/manage-order.component';
import { ManageOrderModule } from '../../components/manage-order/manage-order.module';
import { DetailsComponent } from './details.component';

const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MaterialModule, MatTooltipModule];
const TEI_MATERIAL_MODULES = [DynamicFormModule, ManageCategoryModule, SnackBarModule, ManageOrderModule, LoadingModule];

@NgModule({
    declarations: [DetailsComponent],
    imports: [
        RouterModule.forChild([{ path: '', component: DetailsComponent }]),
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...TEI_MATERIAL_MODULES
    ],
    providers: [{ provide: 'IDetailsService', useClass: DetailsService }]
})
export class DetailsModule { }
