import { NgModule } from '@angular/core';
import { ManageOrderComponent } from './manage-order.component';
import { ManageOrderService } from '../../@services/manage-order.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MaterialModule } from '../dynamic-components/material.module';
import { CommonModule } from '@angular/common';

const BASE_MODULES = [FormsModule, ReactiveFormsModule,CommonModule];
const MATERIAL_MODULES = [MaterialModule];
const TEI_MATERIAL_MODULES = [];

const COMPONENTS = [ManageOrderComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IManageOrderService', useClass: ManageOrderService }],
})
export class ManageOrderModule { }
