import { HttpClient } from "@angular/common/http";
import { Inject, Injectable } from "@angular/core";
import { IAPIBaseUrlProvider } from "@tei/core";
import { Observable } from "rxjs";
import { DeleteTemplateDataRequest, DeleteTemplateDataResponse } from "../@intermediate/booking-requirements";
import { IBookingrequirementsService } from "../@intermediate/booking-requirements/booking-requirements.interface";
import { GetBookingRequirementsTemplateDataRequest, GetBookingRequirementsTemplateDataResponse } from "../@intermediate/booking-requirements/models/get-booking-requirements.model";
import { PostBookingRequirementsRequest, PostBookingRequirementsResponse } from "../@intermediate/booking-requirements/models/post-booking-requirements.model";

@Injectable({
  providedIn: 'root'
})

export class BookingrequirementsService implements IBookingrequirementsService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/BookingRequirements/BookingRequirementsTemplate';
  }

  Post(request: PostBookingRequirementsRequest): Observable<PostBookingRequirementsResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostBookingRequirementsResponse>(apiUrl, request);
  }

  public Get(request: GetBookingRequirementsTemplateDataRequest): Observable<GetBookingRequirementsTemplateDataResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetBookingRequirementsTemplateDataResponse>(apiUrl, { params: request as any });
  }

  public Delete(request: DeleteTemplateDataRequest): Observable<DeleteTemplateDataResponse>
  {
    const apiUrl = this.serviceUrl + "/Delete";
    return this.http.post<DeleteTemplateDataResponse>(apiUrl, request);
  }
}
