import { Component, OnInit } from '@angular/core';
import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { LegacyTooltipPosition as TooltipPosition } from '@angular/material/legacy-tooltip';
import { ComponentLookup } from '../component-lookup';
import { FieldConfig } from '../field.interface';

@ComponentLookup('permissionstable')
@Component({
  selector: 'tei-common-module-settings-permissionstable',
  templateUrl: './permissionstable.component.html',
  styleUrls: ['./permissionstable.component.scss']
})
export class PermissionsTableComponent implements OnInit
{
  field: FieldConfig;
  group: UntypedFormGroup;
  positionOptions: TooltipPosition[] = ['below', 'above', 'left', 'right'];
  position = new UntypedFormControl(this.positionOptions[2]);
  dataSource: any;
  displayedColumns: string[] = [];
  table: any[][] = [];
  constructor()
  {

  }

  ngOnInit()
  {

    this.field.roles.forEach(f =>
    {
      this.displayedColumns.push(f.roleName);
    });

    this.field.permissionsTableColletion.forEach(element =>
    {

      let rolesTable: any[][] = [];
      element.forEach(e =>
      {
        e.formControlView = 1;
        this.field.roles.forEach(r =>
        {

          const c = r.roleName;

          if (r.roleID == -1)
          {
            var v = element.filter(g => g.label != "");
            var toolTip = element.filter(g => g.toolTip != "");
            if (v.length > 0)
            {
              rolesTable[c] = { SettingName: v[0].label, ToolTip: toolTip[0].toolTip };
              // element.forEach(g => g.label = "");
            }
          }
          else
          {
            if (e.roleID == r.roleID)
            {
              rolesTable[c] = e;
            }
          }

        });
      });
      this.table.push(rolesTable);
    });
    this.dataSource = new MatTableDataSource<any>(this.table);

  }
}

