import { ComponentFactory, ComponentFactoryResolver, Directive, Input, ViewContainerRef } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { AutocompleteComponent } from '../autocomplete/autocomplete.component';
import { BookingRequirementsListComponent } from '../booking-requirements-list/booking-requirements-list.component';
import { CardLinkComponent } from '../cardlink/cardlink.component';
import { CheckboxComponent } from '../checkbox/checkbox.component';
import { ChipsComponent } from '../chips/chips.component';
import { ComponentLookupRegistry } from '../component-lookup';
import { DateComponent } from '../date/date.component';
import { FieldConfig } from '../field.interface';
import { ImageUploadComponent } from '../imageupload/image-upload.component';
import { InputComponent } from '../input/input.component';
import { LinkSettingComponent } from '../linksetting/linksetting.component';
import { PaymentPickupLocationComponent } from '../paymentpickuplocation/paymentpickuplocation.component';
import { PaymentShippingZoneComponent } from '../paymentshippingzone/paymentshippingzone.component';
import { QuillComponent } from '../quill/quill.component';
import { RadiobuttonComponent } from '../radiobutton/radiobutton.component';
import { SelectComponent } from '../select/select.component';
import { SelectionListComponent } from '../selectionlist/selectionlist.component';
import { SliderComponent } from '../slider/slider.component';
import { SlidetoggleComponent } from '../slidetoggle/slidetoggle.component';
import { TeeTimeCourseDetailsComponent } from '../teetimecoursedetails/teetimecoursedetails.component';
import { TogglebuttonComponent } from '../togglebutton/togglebutton.component';
import { RestaurantSettingComponent } from '../restaurantsetting/restaurantsetting.component';


const componentMapper = {
  input: InputComponent,
  select: SelectComponent,
  date: DateComponent,
  radiobutton: RadiobuttonComponent,
  checkbox: CheckboxComponent,
  toggle: TogglebuttonComponent,
  slidetoggle: SlidetoggleComponent,
  chip: ChipsComponent,
  selectionlist: SelectionListComponent,
  cardlink: CardLinkComponent,
  quill: QuillComponent,
  paymentshippingzone: PaymentShippingZoneComponent,
  autocomplete: AutocompleteComponent,
  paymentpickuplocation: PaymentPickupLocationComponent,
  slider: SliderComponent,
  teetimecoursedetails: TeeTimeCourseDetailsComponent,
  imageupload: ImageUploadComponent,
  linksetting: LinkSettingComponent,
  bookingrequirementslist: BookingRequirementsListComponent,
  restaurantsetting: RestaurantSettingComponent
};
@Directive({
  selector: '[teiModuleSettingsDynamicField]'
})
export class DynamicFieldDirective
{
  @Input() field: FieldConfig;
  @Input() group: UntypedFormGroup;

  componentRef: any;
  constructor(
    private resolver: ComponentFactoryResolver,
    private container: ViewContainerRef
  ) { }

  ngOnInit()
  {
    try
    {
      let factory: ComponentFactory<unknown>;

      if (this.field.type === 'card' || this.field.type === 'permissionstable' || this.field.type === 'expansionpanel')
      {
        const classRef = ComponentLookupRegistry.get(this.field.type);

        factory = this.resolver.resolveComponentFactory(
          classRef
        );
      } else
      {
        factory = this.resolver.resolveComponentFactory(
          componentMapper[this.field.type]
        );
      }

      this.componentRef = this.container.createComponent(factory);
      this.componentRef.instance.field = this.field;
      this.componentRef.instance.group = this.group;
    } catch (ex)
    {
      console.log(this.field);
      throw ex;

    }
  }
}
