import { UntypedFormControl, UntypedFormGroup } from '@angular/forms';
import { FieldConfig } from './../../../../lib/components/dynamic-components/field.interface';
import { GetManageOrderResponse, ModuleSettings_FieldItem } from './get-manage-order.model';

export class PostManageOrderRequest
{
  public ID: string;
  public Type: string;
  public ListFieldItems: Array<ModuleSettings_FieldItem>;

}

export class PostManageOrderRequestForm extends UntypedFormGroup
{
  constructor()
  {
    super({
      ID: new UntypedFormControl(''),
      Type: new UntypedFormControl(''),
      ListFieldItems: new UntypedFormControl(''),
    });
  }

  public value: PostManageOrderRequest;

  public setValue(
    value: GetManageOrderResponse,
    options?: {
      onlySelf?: boolean;
      emitEvent?: boolean;
    }): void
  {
    const request = new PostManageOrderRequest();
    request.ID = value.ID;
    request.Type = value.Type;
    request.ListFieldItems = value.ListFieldItems;
    super.setValue(request, options);
  }
}

export class PostManageOrderResponse
{
  public fieldConfigs: FieldConfig[];
}
