import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core'
import { IManageCategoryService, GetManageCategoryRequest, GetManageCategoryResponse, PostManageCategoryRequest, PostManageCategoryResponse } from '../@intermediate/manage-category';

@Injectable({
  providedIn: 'root'
})

export class ManageCategoryService implements IManageCategoryService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/ModuleSettings/' + 'ManageCategory';
  }

  public Get(request: GetManageCategoryRequest): Observable<GetManageCategoryResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetManageCategoryResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostManageCategoryRequest): Observable<PostManageCategoryResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostManageCategoryResponse>(apiUrl, request);
  }
}
