import { NgModule } from '@angular/core';
import { ManageCategoryComponent } from './manage-category.component';
import { ManageCategoryService } from '../../@services/manage-category.service';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { CommonModule } from '@angular/common';
import { MaterialModule } from '../dynamic-components/material.module';

const BASE_MODULES = [FormsModule, ReactiveFormsModule, CommonModule];
const MATERIAL_MODULES = [MaterialModule];
const TEI_MATERIAL_MODULES = [];

const COMPONENTS = [ManageCategoryComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'IManageCategoryService', useClass: ManageCategoryService }],
})
export class ManageCategoryModule { }
