import { CommonModule } from "@angular/common";
import { NgModule } from "@angular/core";
import { FormsModule, ReactiveFormsModule } from "@angular/forms";
import { MatLegacyButtonModule as MatButtonModule } from "@angular/material/legacy-button";
import { MatLegacyDialogModule as MatDialogModule } from "@angular/material/legacy-dialog";
import { MatIconModule } from "@angular/material/icon";
import { MatLegacyInputModule as MatInputModule } from "@angular/material/legacy-input";
import { MatLegacyListModule as MatListModule } from "@angular/material/legacy-list";
import { RouterModule } from "@angular/router";
import { MatLegacySelectModule as MatSelectModule } from '@angular/material/legacy-select';
import { TemplateDialogService } from "../../@services/template-dialog-service";
import { TemplateDialogComponent } from "./template-dialog.component";

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule, RouterModule];
const MATERIAL_MODULES = [MatButtonModule, MatDialogModule, MatInputModule, MatIconModule, MatButtonModule, MatSelectModule, MatListModule];
const TEI_MATERIAL_MODULES = [];

const COMPONENTS = [TemplateDialogComponent];

@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS
  ],
  providers: [{ provide: 'ITemplateDialogService', useClass: TemplateDialogService }],
})
export class TemplateDialogModule { }
