import { Component, Inject, OnInit, SkipSelf } from '@angular/core';
import { UntypedFormArray, UntypedFormBuilder } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { DialogData } from '@tei/material';
import { GetManageCategoryRequest, GetManageCategoryResponse, IManageCategoryService, PostManageCategoryRequestForm } from '../../@intermediate/manage-category';

export class ManageCategoryDialogData extends DialogData
{
  refID: string;
  settingCategoryID: string;
}

@Component({
  selector: 'tei-common-module-settings-manage-category',
  templateUrl: './manage-category.component.html',
  styleUrls: ['./manage-category.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(ManageCategoryComponent)
  ]
})
export class ManageCategoryComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetManageCategoryResponse;
  form: PostManageCategoryRequestForm;

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('IManageCategoryService') private ManageCategoryService: IManageCategoryService,
    @Inject(MAT_DIALOG_DATA) public data: ManageCategoryDialogData,
    private formBuilder: UntypedFormBuilder,
    public dialogRef: MatDialogRef<ManageCategoryComponent>)
  {
    super(_errorStateMatcher);
  }

  ngOnInit()
  {
    this.form = new PostManageCategoryRequestForm(this.ManageCategoryService);
    const request = new GetManageCategoryRequest();
    this.load(request);

    this.form.controls.DisplayType.valueChanges.subscribe(value =>
    {
      this.form.Properties.setValue(this.model.Properties.filter(x => x.Filter == value));
    });
  }

  private load(request: GetManageCategoryRequest)
  {
    if (!this.model)
    {
      this.model = new GetManageCategoryResponse();
    }
    request.ID = this.data.settingCategoryID; //'EB547DB6-97AB-49BA-AADE-2C25892D027E';
    this.ManageCategoryService.Get(request).subscribe(response =>
    {
      response.Type = 'category';
      response.NavigationID = this.data.settingCategoryID;
      response.DependencyCondition = '';

      if (this.data.refID)
      {
        response.SubRefID = this.data.refID;
      } else
      {
        response.SubRefID = null;
      }
      this.model = response;
      this.writeValue(response);
    });
  }

  save(form: PostManageCategoryRequestForm)
  {

    this.ManageCategoryService.Post(form.value).subscribe(response =>
    {
      this.dialogRef.close({ data: response.fieldConfigs });
    });
  }

  get ManagedParentCategory()
  {
    return this.model.Categories.filter(x => x.Type == 'category' && x.Filter === "1");
  }

  get TypesFilter()
  {
    if (this.model.Types)
    {
      if (this.form.controls.MoreTypes.value)
      {
        return this.model.Types.filter(x => x.Filter == this.form.value.Type);
      }
      else
      {
        return this.model.Types.filter(x => x.Filter == this.form.value.Type && !x.Name.endsWith("(Custom)"));
      }
    }
    else
    {
      return null;
    }
  }

  get ShowOptionsCapture()
  {
    if (this.form.value.DisplayType)
    {
      return this.model.Types.filter(x => x.ID == this.form.value.DisplayType)[0].Selected;
    }
    else
    {
      return false;
    }
  }

  addOption()
  {
    var items = this.form.get('Options') as UntypedFormArray;
    items.push(this.formBuilder.group({
      Name: '',
      Value: '',
    }));
  }

  addDependency()
  {
    var items = this.form.get('Dependencies') as UntypedFormArray;
    items.push(this.formBuilder.group({
      ID: '',
      Value: '',
      Filter: ''
    }));
  }
}

