import { Inject, Injectable } from '@angular/core';
import { Observable } from 'rxjs';
import { HttpClient, HttpParams } from '@angular/common/http';
import { IAPIBaseUrlProvider } from '@tei/core'
import { IManageOrderService, GetManageOrderRequest, GetManageOrderResponse, PostManageOrderRequest, PostManageOrderResponse } from '../@intermediate/manage-order';

@Injectable({
  providedIn: 'root'
})

export class ManageOrderService implements IManageOrderService
{
  private readonly serviceUrl: string;

  constructor(@Inject('IAPIBaseUrlProvider') protected apiBaseUrlProvider: IAPIBaseUrlProvider, private http: HttpClient)
  {
    this.serviceUrl = this.apiBaseUrlProvider.getBaseUrl() + '/Common/ModuleSettings/' + 'ManageOrder';
  }

  public Get(request: GetManageOrderRequest): Observable<GetManageOrderResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.get<GetManageOrderResponse>(apiUrl, { params: request as any });
  }

  public Post(request: PostManageOrderRequest): Observable<PostManageOrderResponse>
  {
    const apiUrl = this.serviceUrl + '';
    return this.http.post<PostManageOrderResponse>(apiUrl, request);
  }
}
