import { NgModule } from '@angular/core';
import { OverviewComponent } from './overview.component';
import { OverviewService } from '../../@services/overview.service';
import { RouterModule } from '@angular/router';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { BreadCrumbModule, LoadingModule, MaterialModule } from '@tei/material';
import { MatIconModule } from '@angular/material/icon';
import { ManageOverviewModule } from '../../components/manage-overview/manage-overview.module';
import { ManageOverviewComponent } from '../../components/manage-overview/manage-overview.component';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { DynamicFormModule } from '../../components/dynamic-components/dynamic-form/dynamic-form.module';
import { ManageOrderModule } from '../../components/manage-order/manage-order.module';
import { ManageOrderComponent } from '../../components/manage-order/manage-order.component';

const BASE_MODULES = [FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MaterialModule, MatIconModule, MatDialogModule, LoadingModule];
const TEI_MATERIAL_MODULES = [ManageOverviewModule, ManageOrderModule, DynamicFormModule, BreadCrumbModule];

@NgModule({
    declarations: [OverviewComponent],
    imports: [
        RouterModule.forChild([{ path: '', component: OverviewComponent }]),
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...TEI_MATERIAL_MODULES
    ],
    providers: [{ provide: 'IOverviewService', useClass: OverviewService }]
})
export class OverviewModule { }
