import { Component, Inject, OnInit, SkipSelf } from '@angular/core';
import { UntypedFormArray, UntypedFormBuilder, Validators } from '@angular/forms';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { AbstractFormGroupControlValueAccessor, ngFormGroupControlValueAccessorProviders } from '@tei/core';
import { DialogData } from '@tei/material';
import { GetManageOverviewRequest, GetManageOverviewResponse, IManageOverviewService, PostManageOverviewRequestForm } from '../../@intermediate/manage-overview';

@Component({
  selector: 'tei-common-module-settings-manage-overview',
  templateUrl: './manage-overview.component.html',
  styleUrls: ['./manage-overview.component.scss'],
  providers: [
    ngFormGroupControlValueAccessorProviders(ManageOverviewComponent)
  ]
})
export class ManageOverviewComponent extends AbstractFormGroupControlValueAccessor implements OnInit
{
  model: GetManageOverviewResponse;
  form: PostManageOverviewRequestForm;
  parentId: string;

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('IManageOverviewService') private ManageOverviewService: IManageOverviewService,
    @Inject(MAT_DIALOG_DATA) public data: DialogData,
    private formBuilder: UntypedFormBuilder,
    public dialogRef: MatDialogRef<ManageOverviewComponent>)
  {
    super(_errorStateMatcher);
  }

  ngOnInit()
  {
    this.form = new PostManageOverviewRequestForm(this.ManageOverviewService);
    const request = new GetManageOverviewRequest();
    this.load(request);

    this.form.controls["Navigation"].valueChanges.subscribe(s =>
    {
      if (s === 'link')
      {
        this.form.get("PageLink").setValidators(Validators.required);
      }
      else
      {
        this.form.get("PageLink").clearValidators();
      }
      this.form.get("PageLink").updateValueAndValidity();
    });

  }

  private load(request: GetManageOverviewRequest)
  {
    if (!this.model)
    {
      this.model = new GetManageOverviewResponse();
    }

    this.ManageOverviewService.Get(request).subscribe(response =>
    {
      this.model = response;
      response.ParentID = this.data.message;
      response.Navigation = 'detail';
      response.VersionIdentifier = '';
      response.DependencyCondition = '';
      this.writeValue(this.model);
    });
  }

  save(form)
  {
    this.ManageOverviewService.Post(form.value).subscribe(response =>
    {
      this.dialogRef.close({ data: response.fieldConfigs });
    });
  }

  addDependency()
  {
    var items = this.form.get('Dependencies') as UntypedFormArray;
    items.push(this.formBuilder.group({
      ID: ''
    }));
  }
}
