import { Component, OnInit, EventEmitter } from '@angular/core';
import { FieldConfig } from '../field.interface';
import { UntypedFormGroup } from '@angular/forms';
import { Options } from '@angular-slider/ngx-slider';
import { DatePipe } from '@angular/common';


@Component({
  selector: 'tei-material-slider',
  templateUrl: './slider.component.html',
  styleUrls: ['./slider.component.scss']
})
export class SliderComponent implements OnInit
{
  field: FieldConfig;
  group: UntypedFormGroup;
  dateformat: string;
  append: string;
  prepend: string;
  ceilprepend: string;
  ceilappend: string;
  options: Options = {};
  slidersRefresh: EventEmitter<void> = new EventEmitter<void>();
  constructor(private datePipe: DatePipe) { }

  ngOnInit()
  {
    this.dateformat = (this.field.options.find(x => x.display == 'dateformat')) ? this.field.options.find(x => x.display == 'dateformat').value : 'hh a';
    this.append = (this.field.options.find(x => x.display == 'append')) ? this.field.options.find(x => x.display == 'append').value : '';
    this.prepend = (this.field.options.find(x => x.display == 'prepend')) ? this.field.options.find(x => x.display == 'prepend').value : '$';
    this.ceilprepend = (this.field.options.find(x => x.display == 'ceilprepend')) ? this.field.options.find(x => x.display == 'ceilprepend').value : '+';
    this.ceilappend = (this.field.options.find(x => x.display == 'ceilappend')) ? this.field.options.find(x => x.display == 'ceilappend').value : '+';
    this.options.floor = this.field.options.length > 0 ? Number(this.field.options.find(x => x.display == 'floor').value) : 5;
    this.options.ceil = this.field.options.length > 0 ? Number(this.field.options.find(x => x.display == 'ceil').value) : 150;
    this.options.step = this.field.options.length > 0 ? Number(this.field.options.find(x => x.display == 'step').value) : 5;
    this.options.showTicks = (this.field.options.find(x => x.display == 'showTicks')) ? Boolean(this.field.options.find(x => x.display == 'showTicks').value) : false;
    this.options.translate = (value: number): string =>
    {
      return (value == this.options.ceil ? this.ceilprepend : '') + this.prepend + (this.dateformat !== '' ? this.datePipe.transform(new Date(value), this.dateformat) : value) + this.append + (value == this.options.ceil ? this.ceilappend : '');
    };
    setTimeout(() =>
    {
      this.slidersRefresh.emit();
    }, 200);
  }
}
