import { Component, Inject, OnInit, ViewChild } from '@angular/core';
import { MatLegacyDialogRef as MatDialogRef, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { ImageCroppedEvent, ImageCropperComponent } from 'ngx-image-cropper';
export class ImgDialogData
{
  origImage: string;
  croppedImage: string;
  imgName: string;
}
@Component({
  selector: 'tei-access-management-image-crop-dialog',
  templateUrl: './image-crop-dialog.component.html',
  styleUrls: ['./image-crop-dialog.component.scss']
})
export class ImageCropDialogComponent implements OnInit
{
  @ViewChild(ImageCropperComponent) imageCropper: ImageCropperComponent;

  imageChangedEvent: any = '';
  croppedImage: any = '';
  origImage: any = '';
  imgName: string;
  imageError: string;
  constructor(
    public dialogRef: MatDialogRef<ImageCropDialogComponent>,
    @Inject(MAT_DIALOG_DATA) public data: ImgDialogData
  )
  {
    if (data)
    {
      this.origImage = data.origImage;
      this.croppedImage = data.croppedImage;
    }
  }

  confirm()
  {
    const data = new ImgDialogData();
    data.imgName = this.imgName;

    data.origImage = this.croppedImage; //this.imageCropper.originalBase64; to do
    data.croppedImage = this.croppedImage;
    this.dialogRef.close(data);
  }

  onNoClick(): void
  {
    this.dialogRef.close();
  }

  ngOnInit()
  {
  }

  fileChangeEvent(event: any)
  {
    const allowed_types = ['image/png', 'image/bmp', 'image/jpeg', 'image/gif'];
    if (!allowed_types.includes(event.target.files[0].type))
    {
      this.imageError = 'Only Images are allowed ( JPG | PNG | GIF | BMP )';
      return false;
    }
    if (event.target.files[0].size < 1000000)
    {
      this.imgName = event.target.files[0].name;
      this.imageChangedEvent = event;
    }
    else
    {
      this.imageError = "The image file size cannot exceed 1MB";
      return false;
    }
  }

  imageCropped(event: ImageCroppedEvent)
  {
    this.croppedImage = event.base64;
    //this.croppedImage = base64ToFile(event.base64);
  }

  imageLoaded()
  {
  }

  cropperReady()
  {
    // cropper ready
  }

  loadImageFailed()
  {
    // show message
  }
}
