import { Component, OnInit } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { Router } from '@angular/router';
import { FieldConfig } from '../field.interface';

@Component({
  selector: 'tei-module-link-settings',
  templateUrl: './linksetting.component.html',
  styleUrls: ['./linksetting.component.scss']
})
export class LinkSettingComponent implements OnInit {
  field: FieldConfig;
  group: UntypedFormGroup;

  constructor(private router: Router) {

  }

  ngOnInit() {

  }

  onCardClick() {
    if (!this.getFieldPropertyValue('disabled')) {
      //this.router.navigate(['/office/module-settings/detail/' + this.field.name]);
      let orign = window.location.origin;
      // let pathname = window.location.pathname;
      let details = orign + "/office/portal-admin-center/setup/web-admin-center/detail/" + this.field.name;
      window.location.href = details;
      //window.location.reload();
    }
  }

  getFieldPropertyValue(input: string) {
    return this.field && this.field.properties && this.field.properties.length > 0 && this.field.properties.find(x => x.name == input) ? this.field.properties.find(x => x.name == input).value : "";
  }

  getCSS() {
    return "width: 300px; height: 140px; margin:5px; float: left; " + (this.getFieldPropertyValue('disabled') ? "background: (0,0,0,0.4); opacity: 0.7;" : "cursor:pointer;");
  }
}
