import { ScrollingModule } from '@angular/cdk/scrolling';
import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatLegacyDialogModule as MatDialogModule, MAT_LEGACY_DIALOG_DATA as MAT_DIALOG_DATA } from '@angular/material/legacy-dialog';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatLegacyTooltipModule as MatTooltipModule } from '@angular/material/legacy-tooltip';
// import { ViewPickupLocationModule, ViewShippingDetailsModule } from '@tei/payment-engine-shared';
import { ViewCourseDetailsModule } from '@tei/booking-tee-times-shared';
import { BookingRequirementsListModule } from '@tei/common-booking-requirements-shared';
import { TeiFakePasswordTypeModule } from '@tei/core';
import { NgxSliderModule } from '@angular-slider/ngx-slider';
import { NgxDaterangepickerMd } from 'ngx-daterangepicker-material';
import { QuillModule } from 'ngx-quill';
import { AutocompleteComponent } from '../autocomplete/autocomplete.component';
import { BookingRequirementsListComponent } from '../booking-requirements-list/booking-requirements-list.component';
import { CardComponent } from '../card/card.component';
import { CardLinkComponent } from '../cardlink/cardlink.component';
import { CheckboxComponent } from '../checkbox/checkbox.component';
import { ChipsComponent } from '../chips/chips.component';
import { DateComponent } from '../date/date.component';
import { DynamicFieldDirective } from '../dynamic-field/dynamic-field.directive';
import { ExpansionPanelComponent } from '../expansionpanel/expansionpanel.component';
import { InputComponent } from '../input/input.component';
import { MaterialModule } from '../material.module';
import { PaymentPickupLocationComponent } from '../paymentpickuplocation/paymentpickuplocation.component';
import { PaymentShippingZoneComponent } from '../paymentshippingzone/paymentshippingzone.component';
import { PermissionsTableComponent } from '../permissionstable/permissionstable.component';
import { QuillComponent } from '../quill/quill.component';
import { RadiobuttonComponent } from '../radiobutton/radiobutton.component';
import { SelectComponent } from '../select/select.component';
import { SelectionListComponent } from '../selectionlist/selectionlist.component';
import { SliderComponent } from '../slider/slider.component';
import { SlidetoggleComponent } from '../slidetoggle/slidetoggle.component';
import { TeeTimeCourseDetailsComponent } from '../teetimecoursedetails/teetimecoursedetails.component';
import { Safe, TogglebuttonComponent } from '../togglebutton/togglebutton.component';
import { ImageUploadModule } from './../imageupload/image-upload.module';
import { LinkSettingComponent } from './../linksetting/linksetting.component';
import { DynamicFormComponent } from './dynamic-form.component';
import { DynamicFormService } from './dynamic-form.service';
import { RestaurantSettingComponent } from '../restaurantsetting/restaurantsetting.component';
import { RestaurantSettingModule } from '@tei/booking-dining-shared';

const BASE_MODULES = [CommonModule, FormsModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatDialogModule, MatTooltipModule, MatTableModule, ScrollingModule];
const MODULES = [ImageUploadModule];
const CORE_MODULES = [TeiFakePasswordTypeModule];
const PIPES = [Safe];
const PROVIDERS = [{ provide: MAT_DIALOG_DATA, useValue: {} }, DynamicFormService];

const COMPONENTS = [
  InputComponent,
  ExpansionPanelComponent,
  SelectComponent,
  DateComponent,
  RadiobuttonComponent,
  CheckboxComponent,
  TogglebuttonComponent,
  SlidetoggleComponent,
  ChipsComponent,
  SelectionListComponent,
  CardComponent,
  ExpansionPanelComponent,
  PermissionsTableComponent,
  CardLinkComponent,
  QuillComponent,
  PaymentShippingZoneComponent,
  AutocompleteComponent,
  PaymentPickupLocationComponent,
  SliderComponent,
  PermissionsTableComponent,
  TeeTimeCourseDetailsComponent,
  LinkSettingComponent,
  BookingRequirementsListComponent,
  RestaurantSettingComponent,
];

@NgModule({
    declarations: [...COMPONENTS,
        ...PIPES,
        DynamicFieldDirective,
        DynamicFormComponent],
    imports: [...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...MODULES,
        ...CORE_MODULES,
        NgxDaterangepickerMd.forRoot(),
        QuillModule.forRoot({
            modules: {
                syntax: false,
                toolbar: [
                    ['bold', 'italic', 'underline', 'strike'],
                    ['blockquote', 'code-block'],
                    [{ 'header': 1 }, { 'header': 2 }],
                    [{ 'list': 'ordered' }, { 'list': 'bullet' }],
                    [{ 'script': 'sub' }, { 'script': 'super' }],
                    [{ 'indent': '-1' }, { 'indent': '+1' }],
                    [{ 'direction': 'rtl' }],
                    [{ 'size': ['small', false, 'large', 'huge'] }],
                    [{ 'header': [1, 2, 3, 4, 5, 6, false] }],
                    [{ 'color': [] }, { 'background': [] }],
                    [{ 'font': [] }],
                    [{ 'align': [] }],
                    ['clean'],
                    ['link', 'image', 'video'] // link and image, video
                ]
            }
        }),
        MaterialModule,
        // ViewShippingDetailsModule,
        // ViewPickupLocationModule,
        ViewCourseDetailsModule,
        NgxSliderModule,
        BookingRequirementsListModule,
        RestaurantSettingModule
    ],
    exports: [
        ...BASE_MODULES,
        ...MATERIAL_MODULES,
        ...MODULES,
        ...COMPONENTS,
        ...PIPES,
        DynamicFieldDirective,
        DynamicFormComponent
    ],
    providers: [...PROVIDERS]
})
export class DynamicFormModule { }
