import { Component, OnInit } from '@angular/core';
import { UntypedFormGroup } from '@angular/forms';
import { Observable } from 'rxjs';
import { map, startWith } from 'rxjs/operators';
import { FieldConfig, FieldOption } from '../field.interface';


@Component({
  selector: 'tei-common-module-settings-autocomplete',
  templateUrl: './autocomplete.component.html',
  styleUrls: ['./autocomplete.component.scss'],
  host: { 'class': 'o-setting' }
})
export class AutocompleteComponent implements OnInit
{
  field: FieldConfig;
  group: UntypedFormGroup;
  filteredOptions: Observable<FieldOption[]>;

  constructor()
  {

  }
  ngOnInit()
  {
    this.filteredOptions = this.group.get(this.field.name).valueChanges
      .pipe(
        startWith(''),
        map(value => this._filter(value))
      );
  }

  private _filter(value: string): FieldOption[]
  {
    const filterValue = value.toLowerCase();

    return this.field.options.filter(option => option.display.toLowerCase().includes(filterValue));
  }

}
