import { CommonModule } from '@angular/common';
import { NgModule } from '@angular/core';
import { FormsModule, ReactiveFormsModule } from '@angular/forms';
import { MatIconModule } from '@angular/material/icon';
import { MatLegacyButtonModule as MatButtonModule } from '@angular/material/legacy-button';
import { MatLegacyCardModule as MatCardModule } from '@angular/material/legacy-card';
import { MatLegacyCheckboxModule as MatCheckboxModule } from '@angular/material/legacy-checkbox';
import { MatLegacyDialogModule as MatDialogModule } from '@angular/material/legacy-dialog';
import { MatLegacyListModule as MatListModule } from '@angular/material/legacy-list';
import { MatLegacyMenuModule as MatMenuModule } from '@angular/material/legacy-menu';
import { MatLegacyPaginatorModule as MatPaginatorModule } from '@angular/material/legacy-paginator';
import { MatLegacyTableModule as MatTableModule } from '@angular/material/legacy-table';
import { MatSortModule } from '@angular/material/sort';
import { ResponsiveTableHeightModule } from '@tei/core';
import { ConfirmDialogModule } from '@tei/material';
import { BookingrequirementsService } from '../../@services/booking-requirements.service';
import { TemplateDialogModule } from '../../components/template-dialog/template-dialog.module';
import { BookingRequirementsListComponent } from './booking-requirements-list.component';

const BASE_MODULES = [FormsModule, CommonModule, ReactiveFormsModule];
const MATERIAL_MODULES = [MatSortModule, MatDialogModule, MatCheckboxModule, MatCardModule, MatListModule,
  MatTableModule, MatPaginatorModule, MatIconModule, MatButtonModule, MatMenuModule];
const TEI_MATERIAL_MODULES = [TemplateDialogModule, ConfirmDialogModule];
const COMPONENTS = [BookingRequirementsListComponent];
const TEI_CORE_MODULES = [ResponsiveTableHeightModule];
@NgModule({
  declarations: [...COMPONENTS],
  imports: [
    // RouterModule.forChild([{ path: '', component: BookingRequirementsListComponent }]),
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...TEI_CORE_MODULES
  ],
  exports: [
    ...BASE_MODULES,
    ...MATERIAL_MODULES,
    ...TEI_MATERIAL_MODULES,
    ...COMPONENTS,
    ...TEI_CORE_MODULES
  ],
  providers: [{ provide: 'IBookingRequirementsService', useClass: BookingrequirementsService }],
})
export class BookingRequirementsListModule { }
