import { Component, EventEmitter, Inject, Input, OnInit, Output, SkipSelf, ViewChild } from '@angular/core';
import { ErrorStateMatcher } from '@angular/material/core';
import { MatLegacyDialog as MatDialog } from '@angular/material/legacy-dialog';
import { MatLegacyPaginator as MatPaginator } from '@angular/material/legacy-paginator';
import { MatLegacyTableDataSource as MatTableDataSource } from '@angular/material/legacy-table';
import { MatSort } from '@angular/material/sort';
import { ConfirmDialogService, DialogData } from '@tei/material';
import { DeleteTemplateDataRequest, IBookingrequirementsService } from '../../@intermediate/booking-requirements';
import { BookingRequirementsMetadata, GetBookingRequirementsForm, GetBookingRequirementsTemplateDataRequest, GetBookingRequirementsTemplateDataResponse, Template } from '../../@intermediate/booking-requirements/models/get-booking-requirements.model';

import { ResponsiveService } from '@tei/core';
import { TemplateDialogComponent } from '../../components/template-dialog/template-dialog.component';

@Component({
  selector: 'tei-booking-requirements-shared-booking-requirements-list',
  templateUrl: './booking-requirements-list.component.html',
  styleUrls: ['./booking-requirements-list.component.scss']
})
export class BookingRequirementsListComponent implements OnInit
{
  @ViewChild(MatPaginator, { static: true }) paginator: MatPaginator;
  @ViewChild(MatSort, { static: true }) sort: MatSort;
  @Input() parentSettingCategoryID: string;

  form: GetBookingRequirementsForm;
  template: Template;

  model: GetBookingRequirementsTemplateDataResponse;
  datasource: MatTableDataSource<Template>;
  metadata = new BookingRequirementsMetadata();
  @Output() moduleSettings = new EventEmitter<any>();
  isTablet: boolean;
  isMobile: boolean;
  isSmallScreen: boolean;
  isMediumScreen: boolean;

  constructor(
    @SkipSelf() protected _errorStateMatcher: ErrorStateMatcher,
    @Inject('IBookingRequirementsService') private BookingRequirementsService: IBookingrequirementsService,
    private confirmdialogService: ConfirmDialogService,
    private responsiveService: ResponsiveService,
    public dialog: MatDialog)
  {
    this.form = new GetBookingRequirementsForm();
  }

  ngOnInit(): void
  {
    let request = new GetBookingRequirementsTemplateDataRequest();
    request.ParentSettingCategoryID = this.parentSettingCategoryID;
    this.getMobileSize();
    this.load(request);
  }

  private load(request: GetBookingRequirementsTemplateDataRequest)
  {
    if (!this.model)
    {
      this.model = new GetBookingRequirementsTemplateDataResponse();
    }

    this.BookingRequirementsService.Get(request).subscribe(response =>
    {
      this.model = response;
      this.form.setValue(this.model);
      this.bindBookingRequirements(this.model.Templates);
    }, err => console.log(err));
  }

  private bindBookingRequirements(bookingRequirements: Template[])
  {
    this.datasource = new MatTableDataSource<Template>(bookingRequirements);
    this.datasource.paginator = this.paginator;
    this.datasource.sort = this.sort;
  }
  getMobileSize(): void
  {
    const screenSize = this.responsiveService.mediaBreakpoint.getValue();
    const screenWidth = this.responsiveService.screenWidth.getValue();
    this.isMobile = screenSize === 'xs';
    this.isTablet = screenSize === 'xs' || screenSize === 'sm' || screenSize === 'md';
    this.isSmallScreen = screenWidth <= 600;
    this.isMediumScreen = screenWidth <= 960;
  }
  openDialog(template: Template = null): void
  {
    let response: GetBookingRequirementsTemplateDataResponse = new GetBookingRequirementsTemplateDataResponse();
    response.Templates = [];
    if (!template)
    {
      template = new Template();
      template.Description = null;
      template.TemplateID = null;
      template.TemplateUID = null;
      template.Name = null;
    }
    response.Templates.push(template);
    response.TemplateTypes = this.model.TemplateTypes;

    const dialogRef = this.dialog.open(TemplateDialogComponent, {
      panelClass: ['o-dialog-sm-fullscreen', 'o-dialog-md-fullscreen'],
      width: '300px',
      data: response
    });

    dialogRef.afterClosed().subscribe((result: Template) =>
    {
      this.template = result;
      this.ngOnInit();
    });
  }

  openDeleteDialog(TemplateID: number)
  {
    const index = this.model.Templates.findIndex(f => f.TemplateID == TemplateID);
    const template = this.model.Templates.find(f => f.TemplateID == TemplateID);

    const dialogData: DialogData = {
      title: 'Delete',
      message: `Delete the ${ template.Name } template?`,
      textOkayButton: 'Delete',
      textCancelButton: 'Cancel'
    };

    this.confirmdialogService.openDialog(dialogData, { disableClose: true }).afterClosed().subscribe(result =>
    {
      if (result)
      {
        let request = new DeleteTemplateDataRequest();
        request.TemplateID = template.TemplateID;
        this.BookingRequirementsService.Delete(request).subscribe(response =>
        {
          this.model.Templates.splice(index, 1);
          this.bindBookingRequirements(this.model.Templates);
        });
      }
    });

  }
}
